body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.box {
  display: flex;
  box-shadow: 4px 4px 25px;
  flex-direction: column;
  width: 80vw;
  height: 60vh;
  margin-left: 9vw;
  margin-top: 10vh;
}
.firsthalf {
  position: relative;
  background-color: rgb(23, 203, 142);
  display: flex;
  height: 35vh;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.tempfields {
  display: flex;
  flex-direction: column;
  padding-right: 50px;
  font-size: 25px;
}
.secondhalf {
  display: flex;
  justify-content: space-around;
  height: 25vh;
}
.temperature {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.secondhalf button {
  background-color: white;
  /* border: none; */
  cursor: pointer;
  width: calc(100% / 6);
  height: 100%;
}

.secondhalf button:hover {
  background-color: rgb(250, 250, 250);
}
.day {
  border: 1px solid rgb(237, 237, 237);
}
.secondhalf img {
  width: 100px;
  height: 100px;
}
.searchbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  
 
}

.searchbar button {
  /* color: white; */
  background-color: rgb(23, 203, 142);
  cursor: pointer;
  box-shadow: 4px 4px 4px grey;
  /* border:1px solid rgb(237, 237, 237); */
}

.searchbar input {
  box-shadow: 4px 4px 4px grey;
}
.searchbar button:hover {
  background-color: rgb(5, 220, 148);
}

.searchbar input:focus {
  border-color: #ccc;
}